import React, { useState, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { TextField, Button, Box, Grid } from '@mui/material';
import { SERVER_URL } from "../Link";
import AppBarCustom from "./modules/components/AppBarCustom";


function ProposalEdit() {
  const navigate = useNavigate()
  const params = useParams();  // URL  ID   useParams
  const [proposal, setProposal] = useState({
    userId: "",
    title: "",
    expectedTeamMembers: 0,
    expectedTechnologyStack: "",
    plan: "",
    content: "",
    createdTime: "",
    expiredTime: ""
  })

  const [user, setUser] = useState("");

  useEffect(() => {
    if (user !== "") {
      if (user !== "") {
        fetch(SERVER_URL + `proposal/get?proposalId=${params.proposalId ? params.proposalId : ""}`)
          .then((response) => response.json())
          .then((data) => {
            //   (   )
            if (data.userId !== null && data.userId !== user) {
              alert(" .");
              navigate("/");
            } else { //  
              setProposal({
                ...data,
                userId: user,
              });
            }
          })
          .catch((error) => console.log(error));
      } else {
        console.log("Navigating to home page...");
        alert("  .")
        navigate("/signin");
      }
    }
  }, [user]);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    //      0 
    if (name === "expectedTeamMembers" && value < 0) {
      setProposal((prevProposal) => ({
        ...prevProposal,
        [name]: 0,  //   0 
      }));
    } else {
      setProposal((prevProposal) => ({
        ...prevProposal,
        [name]: value,
      }));
    }
  };

  const handleOnSave = () => {
    fetch(SERVER_URL + "proposal/edit", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(proposal),
    })
      .then((response) => response.text())
      .then((data) => {
        console.log(data)
        setProposal();
        navigate('/proposal/list'); //      
      })
      .catch((error) => {
        console.error("Failed to save proposal:", error);
      });
  };

  const handleDeleteClick = () => {
    if (params.proposalId) {
      fetch(SERVER_URL + `proposal/delete`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(proposal)
      })
        .then((response) => {
          if (response.ok) {
            navigate("/proposal/list");
          }
        })//      
        .catch((error) => {
          console.error("Failed to delete proposal:", error);
        });
    } else {
      navigate("/proposal/list");
    }
  };
  return (
    <>
      <AppBarCustom setUser={setUser} />
      <Box sx={{ padding: 3, maxWidth: '1000px', margin: '0 auto' }}>
        <h2 style={{ fontSize: '2rem', fontWeight: 'bold', color: '#3f51b5', marginBottom: '50px' }}> </h2>
        <form>
          <Grid container spacing={1} alignItems="center" marginBottom={10}>
            {/*     */}
            <Grid item xs={12} sx={{ display: 'flex', justifyContent: 'flex-end' }}>
              <Grid container spacing={3}>
                <Grid item xs={6}>
                  <TextField
                    label=" "
                    name="createdTime"
                    value={(String(proposal?.createdTime || "")).substring(0, 10) + " / " + (String(proposal?.createdTime || "")).substring(11, 19)}
                    disabled
                    fullWidth
                    variant="outlined"
                    sx={{ marginBottom: 2, width: '100%' }}
                    InputLabelProps={{
                      shrink: true,
                    }}
                  />
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    label=" "
                    name="expiredTime"
                    type="datetime-local"
                    value={proposal?.expiredTime || ""}
                    onChange={handleInputChange}
                    fullWidth
                    variant="outlined"
                    sx={{ marginBottom: 2, width: '100%' }}
                    InputLabelProps={{
                      shrink: true,
                    }}
                  />
                </Grid>
              </Grid>
            </Grid>
          </Grid>
          <Grid container spacing={3} alignItems="center" marginBottom={5}>
            <Grid item xs={6}>
              <TextField
                label=""
                name="title"
                value={proposal?.title || ""}
                onChange={handleInputChange}
                fullWidth
                variant="outlined"
              />
            </Grid>
            <Grid item xs={6}>
              <TextField
                label="  "
                name="expectedTeamMembers"
                type="number"
                value={proposal?.expectedTeamMembers || 0}
                onChange={handleInputChange}
                fullWidth
                variant="outlined"
                InputProps={{
                  inputMode: 'numeric',
                  min: 0,
                }}
              />
            </Grid>
          </Grid>
          <TextField
            label="  "
            name="expectedTechnologyStack"
            value={proposal?.expectedTechnologyStack || ""}
            onChange={handleInputChange}
            fullWidth
            variant="outlined"
            sx={{ marginBottom: 5 }}
            multiline
            rows={4}
          />
          <TextField
            label=""
            name="plan"
            value={proposal?.plan || ""}
            onChange={handleInputChange}
            fullWidth
            variant="outlined"
            sx={{ marginBottom: 5 }}
            multiline
            rows={6}
          />
          <TextField
            label=""
            name="content"
            value={proposal?.content || ""}
            onChange={handleInputChange}
            fullWidth
            variant="outlined"
            sx={{ marginBottom: 5 }}
            multiline
            rows={12}
          />
          <Button
            variant="contained"
            color="primary"
            onClick={handleOnSave}
            sx={{ marginRight: 2 }}
          >
            
          </Button>
          <Button
            variant="outlined"
            color="error"
            onClick={handleDeleteClick}
          >
            
          </Button>
        </form>
      </Box>
    </>
  )
}
export default ProposalEdit