import React, { useState, useEffect, useRef } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Button, Box, Grid, Card, CardContent, Typography, TextField } from '@mui/material';
import { SERVER_URL } from "../Link";
import AppBarCustom from "./modules/components/AppBarCustom";

function ProposalList() {
  const navigate = useNavigate();
  const params = useParams();

  const [user, setUser] = useState("");
  const [proposals, setProposals] = useState([]);
  const [searchQuery, setSearchQuery] = useState(""); //    
  const [filteredProposals, setFilteredProposals] = useState([]);
  const ref = useRef(null);
  const [page, setPage] = useState(0);

  // ( ) 
  const [maxPage, setMaxPage] = useState(-100);

  const [bg, setBg] = useState(true);

  //    observer 
  useEffect(() => {
    //          
    fetch(SERVER_URL + "proposal/countpage")
      .then(response => response.text())
      .then(data => {
        //      (     )
        setMaxPage((Number)(data) - 15);

      })
      .catch(error => console.log(error))
  }, [])

  useEffect(() => {
    // console.log(maxPage," ")

    //  
    if (maxPage !== -100 && maxPage > 0) {
      let maxPageLet = maxPage
      const observe = new IntersectionObserver(entries => {
        if (entries[0].isIntersecting) {
          if (maxPageLet > 0) {
            maxPageLet -= 15;

            //20  
            setPage(prevPage => prevPage + 1);
          } else {
            //     ,  ,  
            alert("  .")
            setBg(false)
            observe.unobserve(ref.current);
          }
        }
      }, { threshold: 1 });

      //  
      observe.observe(ref.current);

      //   
      return () => {
        if (ref.current) {
          observe.unobserve(ref.current);
        }
      }
    } else if (maxPage !== -100 && maxPage < 15) {
      setBg(false)
    }
  }, [maxPage])

  useEffect(() => {
    fetch(SERVER_URL + `proposal/list?userId=${params.userId ? params.userId : ""}&page=` + page)
      .then((response) => response.json())
      .then((data) => {
        if (Array.isArray(data)) {
          // console.log("Received data is an array:", data);
          setProposals([...proposals, ...data]);  //    
          setFilteredProposals([...filteredProposals, ...data]); //    
        } else {
          console.error("Received data is not an array:", data);
        }
      })
      .catch((error) => console.log(error));
  }, [page]);

  useEffect(() => {
    //     
    // console.log("Filtered Proposals:", filteredProposals);
    if (searchQuery) {
      setFilteredProposals(proposals.filter(proposal =>
        proposal.title.toLowerCase().includes(searchQuery.toLowerCase())
      ));
    } else {
      setFilteredProposals(proposals); //      
    }
  }, [searchQuery, proposals])

  const handleProposalClick = (proposalId) => {
    navigate(`/proposal/view/${proposalId}`); //       
  };

  const handleAddProposalClick = () => {
    const proposalId = null;  // proposalId  null 

    if (proposalId) {
      navigate(`/proposal/edit/${proposalId}`);  // proposalId     
    } else {
      navigate(`/proposal/edit`);  // proposalId      
    }
  };

  const handleSearchChange = (e) => {
    setSearchQuery(e.target.value); //   
  };

  const handleSearch = () => {
    if (searchQuery) {
      setFilteredProposals(proposals.filter(proposal =>
        proposal.title.toLowerCase().includes(searchQuery.toLowerCase())
      ));
    } else {
      setFilteredProposals(proposals); //      
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleSearch(); // Enter    
    }
  };
  return (
    <>
      <AppBarCustom setUser={setUser} />
      <Box sx={{ padding: 3, maxWidth: '1000px', margin: '0 auto' }}>
        {/* h2  ,      */}
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '50px' }}>
          <Box sx={{ display: 'flex', alignItems: 'center', flex: 1 }}>
            {/*  */}
            <TextField
              variant="outlined"
              size="small"
              sx={{ marginRight: '10px' }}  //    
              placeholder="..."
              value={searchQuery} //   
              onChange={handleSearchChange} //     
              onKeyDown={handleKeyPress} // Enter     
            />
            {/*   */}
            <Button
              variant="contained"
              color="primary"
              onClick={handleSearch} //    
              sx={{
                fontSize: '1rem',
                padding: '8px 16px',
                height: '40px',
                minWidth: '120px',
              }}
            >
              
            </Button>
          </Box>
          {/*  */}
          <h2 style={{ fontSize: '2rem', fontWeight: 'bold', color: '#3f51b5', paddingRight: '200px' }}>
             
          </h2>
          {/*    */}
          {
            params.userId === undefined || user === params.userId ? (
              <Button variant="contained" color="primary" onClick={handleAddProposalClick}
                sx={{
                  fontSize: '1.25rem',
                  padding: '12px 24px',
                  height: '48px',
                  minWidth: '150px',
                }}
              >
                 
              </Button>
            ) : null
          }
        </Box>

        {/*    */}
        <Grid container spacing={3}>
          {filteredProposals.length > 0 ? (
            filteredProposals.map((proposal) => (
              <Grid item xs={12} sm={6} md={4} key={proposal.id}>
                <Card sx={{ cursor: 'pointer' }} onClick={() => handleProposalClick(proposal.id)}>
                  <CardContent>
                    <Typography variant="h6">{proposal.title}</Typography>
                    <Typography variant="body2" color="textSecondary">
                        : {proposal.expectedTeamMembers}
                    </Typography>
                    <Typography variant="body2" color="textSecondary">
                       : {(String)(proposal.expiredTime).replace("T", " / ")}
                    </Typography>
                    <Typography variant="body2" color="textSecondary">
                       : {proposal.userId}
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            ))
          ) : (
            <Typography variant="h6" color="textSecondary"> .   .</Typography>
          )}
        </Grid>
      </Box>

      {/*        */}
      <div><img ref={ref} src={bg ? "/static/images/loading.gif" : ""}></img></div>
    </>
  );
}

export default ProposalList;
