import { useEffect, useState } from "react";
import { SERVER_URL } from "../Link";
import AppBarCustom from "./modules/components/AppBarCustom";
import { useNavigate } from "react-router-dom";
import { Button, TextField } from "@mui/material";


export default function PortfolioBoardEdit(){
    //  
    const [ portfolio, setPortfolio ] = useState({
        id:0,
        title:"",
        view:0,
        userId:"",
        path:[],
        goods:0,
    });
    const [ files, setFiles ] = useState([]);
    const [ user, setUser ] = useState("");

    const navigate = useNavigate();


    useEffect(()=>{
        if(user !== ""){
            if(user !== ""){ //   
                //console.log("")
                //   

                fetch(SERVER_URL + "portfolioboard/getone?userId=" + user)
                .then(respones => respones.json())
                .then(data => {
                    setPortfolio({
                        ...data,
                        userId:user
                    });
                    if(data.path){
                        setFiles(data.path);
                    }
                })
                .catch(error => console.log(error))

            }else{
                //console.log("")
                alert("   .")
                navigate("/signin");
            }
        }
    },[user])

    function handleUploadFiles (e, index) {
        const copyFiles = [...files];

        const file = e.target.files[0];

        if (file) {
            // 
            const fileType = file.type;

            if(fileType === "image/png" || fileType === "image/jpeg" || fileType === "image/gif"){
                const reader = new FileReader();

                //      
                reader.onloadend = function () {
                    const base64String = reader.result; // Base64   


                    // 
                    copyFiles[index] = base64String.replace(/^data:image\/(png|jpeg);base64,/, "");;
                    setFiles(copyFiles);
                };
            
                //   base64 
                reader.readAsDataURL(file); // 'data:image/*;base64,'  
            }else{
                alert("png,jpeg,gif  .");
            }
        }

    }

    //
    function handleSavePortfolio() {
        //    
        let cnt = 0;

        if(files.length > 0){ //  

            const formData = new FormData();

            //  JSON    
            formData.append('portfolio',JSON.stringify(portfolio));
            

            // Base64     
            files.forEach((fileBase64, index) => {
                if(fileBase64 !== ""){ //    

                    //    
                    cnt++;
                    
                    // Base64  "data:image/*;base64,"  
                    const file = base64ToFile(fileBase64, `file_${index}.jpg`); //   

                    formData.append('files', file); //  File  
                }
            });

            //    (  ) 
            if(cnt === 0){
                alert("  .");
                return;
            }

            //   
            fetch(SERVER_URL + "portfolioboard/add",{
                method:"POST",
                body:formData
            })
            .then(respones => respones.text())
            .then(data => {
                alert(data)
                if(data === ""){
                    navigate("/")
                }
            })
            .catch(error => console.log(error))

        }else{
            alert("  .");
        }
    }

    // 64  ->   
    function base64ToFile(base64String, fileName) {
        const byteCharacters = atob(base64String); // Base64  
        const byteArrays = [];
    
        // Base64    
        for (let offset = 0; offset < byteCharacters.length; offset++) {
            byteArrays.push(byteCharacters.charCodeAt(offset));
        }
    
        // Blob 
        const byteArray = new Uint8Array(byteArrays);
        const blob = new Blob([byteArray], { type: "image/jpeg" }); //  MIME   
    
        // Blob File 
        const file = new File([blob], fileName, { type: "image/jpeg" }); //  MIME   
        return file;
    }

    function handleAddfile() {
        // 10  
        if(files.length < 10){
            setFiles([...files,""]);
        }
    }

    function handleDeleteFile(e) {
        // console.log(e.target.name, "");
        
        const copyFiles = [...files];
        copyFiles.splice(e.target.name,1);

        setFiles(copyFiles);
    }

    function handleChangeTitle(e){
        setPortfolio({
            ...portfolio,
            title:e.target.value,
        })
    }

    return(
        <div>
            <AppBarCustom  setUser={setUser}></AppBarCustom>
            
            <div className="portfolioBoard_Edit_div">
                <div className="portfolioBoard_EditSub_div"> 
                    <TextField
                        label=""
                        fullWidth
                        margin="normal"
                        name='title'
                        type="text"
                        value={portfolio.title}  // `user.name`  
                        onChange={handleChangeTitle}  //    
                    />

                    { files &&
                    files.map((file,index)=>(
                        <div className="portfolioBoard_EditImgInput_div">
                            <input 
                                type="file" 
                                name={index} 
                                onChange={(e) => handleUploadFiles(e, index)} 
                                onDrop={(e) => handleUploadFiles(e, index)}  
                                style={{
                                    backgroundImage: file ? `url(data:image/jpeg;base64,${file})` : 'none',
                                    backgroundSize: 'cover', //    
                                    backgroundPosition: 'center', //    
                                    width: '1100px',
                                    height: '600px',
                                    border: '1px solid #ccc', //   ()
                            }}></input>
                            <Button color="warning" name={index} onClick={handleDeleteFile}></Button>
                            {/* <img src={`data:image/jpeg;base64,${file}`} width={700} height={500}></img> */}
                        </div>
                    ))}
                    <div className="portfolioBoard_EditButton_div">
                        <Button variant="outlined" onClick={handleAddfile}>   </Button>
                        <Button variant="contained" color="success" onClick={handleSavePortfolio}> </Button>
                    </div>
                </div>
            </div>
        </div>
    );
}