import { useEffect, useState } from "react";
import AppBarCustom from "./modules/components/AppBarCustom";
import { useNavigate } from "react-router-dom";
import { SERVER_URL } from "../Link";
import ChatRoom from "./ChatRoom";

export default function ChatRoomList() {

    const [invitationCode, setInvitationCode] = useState("");
    const [roomName, setRoomName] = useState("");
    const [user, setUser] = useState("");
    const [openChat, setOpenChat] = useState(false);

    const [rooms, setRooms] = useState();

    const [roomInfo, setRoomInfo] = useState();

    const navigate = useNavigate();

    useEffect(() => {
        if (user !== "") {
            if (user !== "") {
                getRooms();
            } else {
                alert("   .");
                navigate("/signin");
            }
        }
    }, [user]);

    function getRooms() {
        fetch(SERVER_URL + "room/get?userId=" + user)
            .then(response => response.json())
            .then(data => {
                setRooms(data);
                setRoomName("");
            })
            .catch(error => console.log(error));
    }

    function handleChangeRoomCode(e) {
        setInvitationCode(e.target.value);
    }

    function handleChangeRoomName(e) {
        setRoomName(e.target.value);
    }

    //  
    function handleCreateRoom() {
        if (roomName !== "") {
            fetch(SERVER_URL + "room/create?userId=" + user + "&roomName=" + roomName)
                .then(response => response.text())
                .then(data => {
                    alert(data);
                    if (data === "") {
                        getRooms();
                    }
                })
                .catch(error => console.log(error));
        } else {
            alert("  .");
        }
    }

    //  
    function handleJoinRoom() {
        if (invitationCode !== "") {
            fetch(SERVER_URL + "room/join?userId=" + user + "&invitationCode=" + invitationCode)
                .then(response => response.text())
                .then(data => {
                    alert(data);
                    if (data === "") {
                        getRooms();
                    }
                })
                .catch(error => console.log(error));
        } else {
            alert("  .");
        }
    }

    //  
    function handleOpenChat(e) {
        setRoomInfo(rooms[e.target.name]);
        setOpenChat(true);
    }

    //  
    function handleCloseChat() {
        setRoomInfo();
        setOpenChat(false);
    }

    return (
        <div className="chat-room-container">
            <AppBarCustom setUser={setUser} />
            {
                !openChat ? (
                    <div>
                        <div className="input-container">
                            <input
                                className="input-field"
                                placeholder=" "
                                onChange={handleChangeRoomName}
                                value={roomName}
                            />
                            <button className="button" onClick={handleCreateRoom}> </button>
                            <br />
                            <input
                                className="input-field"
                                placeholder=" "
                                onChange={handleChangeRoomCode}
                                value={invitationCode}
                            />
                            <button className="button" onClick={handleJoinRoom}></button>
                        </div>
                        <div className="room-list">
                            <p> </p>
                            {
                                rooms && rooms.map((room, index) => (
                                    <div className="room-item" key={index}>
                                        <p>: {room.name}</p>
                                        <p>: {room.createUserId}</p>
                                        <button name={index} onClick={handleOpenChat}></button>
                                    </div>
                                ))
                            }
                        </div>
                    </div>
                ) : (
                    <div className="chat-page">
                        <button className="leave-button" onClick={handleCloseChat}></button>
                        <p> : {roomInfo.invitationCode}</p>
                        <hr />
                        <ChatRoom roomInfo={roomInfo} user={user} />
                    </div>
                )
            }
        </div>
    );
}
