import './AppBarCustom.css';

import React from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import { Button, Drawer, List, ListItem, ListItemText } from '@mui/material';
import { SERVER_URL } from '../../../Link';
import { useCookies } from 'react-cookie'
import { useNavigate } from 'react-router-dom';

export default function AppBarCustom({ setUser = () => {} }) {
    const [signinCheck, setSigninCheck] = React.useState(false);
    const [cookies, setCookie, deleteCookie] = useCookies("LOGIN_MEMBER");
    const [open, setOpen] = React.useState(false);  // Drawer /  

    const navigate = useNavigate();

    //    
    React.useEffect(() => {
        fetch(SERVER_URL + "user/checkcookie", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(cookies),
            credentials: "include",
        })
        .then(response => response.text())
        .then(data => {
            //   
            if (data !== "" && data !== "") { //  
                setSigninCheck(true);
                setUser(data);
            } else {
                setUser("");
            }
        })
        .catch(error => console.log(error))
    }, [cookies, setUser]);

    function handleLogout() {
        if (cookies) {
            fetch(SERVER_URL + "user/logout", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(cookies),
                credentials: "include",
            })
            .then(response => {
                if (response.ok) {
                    deleteCookie("LOGIN_MEMBER");
                    console.log("");
                    navigate("/signin");
                }
            })
            .catch(error => console.log(error));
        }
    }

    function toggleDrawer () {
        setOpen(!open);
    };

    // Drawer   
    const DrawerList = (
        <div>
            <List sx={{
                width:200,
                fontSize:15
            }}>
                <ListItem button onClick={() => navigate('/')} 
                    sx={{
                        '&:hover': {
                            textDecoration:"underline",  //    
                            cursor: 'pointer',
                        },
                    }}>
                    <p></p>
                </ListItem>

                <ListItem ButtonBase onClick={() => navigate('/mypage')} //   ? 
                    sx={{
                        '&:hover': {
                            textDecoration:"underline",  //    
                            cursor: 'pointer',
                        },
                    }}>
                    <p> </p>
                </ListItem>

                <ListItem ButtonBase onClick={() => navigate('/portfolioboard')}
                    sx={{
                        '&:hover': {
                            textDecoration:"underline",  //    
                            cursor: 'pointer',
                        },
                    }}>
                    <p> </p>
                </ListItem>

                <ListItem ButtonBase onClick={() => navigate('/proposal/list')}
                    sx={{
                        '&:hover': {
                            textDecoration:"underline",  //    
                            cursor: 'pointer',
                        },
                    }}>
                    <p> </p>
                </ListItem>

                <ListItem ButtonBase onClick={() => navigate('/chat/roomlist')}
                    sx={{
                        '&:hover': {
                            textDecoration:"underline",  //    
                            cursor: 'pointer',
                        },
                    }}>
                    <p> </p>
                </ListItem>

                <ListItem ButtonBase onClick={() => navigate('/calendar')}
                    sx={{
                        '&:hover': {
                            textDecoration:"underline",  //    
                            cursor: 'pointer',
                        },
                    }}>
                    <p> </p>
                </ListItem>
                {/* <ListItem button onClick={() => navigate('/contact')}>
                    <ListItemText primary="Contact" />
                </ListItem> */}
            </List>
        </div>
    );

    return (
        <AppBar position="static" sx={{ bgcolor: 'black', display: 'flex', flexDirection: 'row' }}>
            <Toolbar sx={{ display: 'flex', flex: 1 }}> 
                {/*   - Drawer  */}
                <Typography variant="h6" sx={{ textAlign: 'left', flexGrow: 1, color:'white'}}>
                    <Button onClick={toggleDrawer} color="inherit">{open ? "X" : ""}</Button>
                </Typography>

                {/*   */}
                <Typography variant="h4" sx={{ textAlign: 'center', flexGrow: 5, bgcolor: 'black' }}>
                    <a href='/' className='custom_a'>MY-PORTFOLIO</a>
                </Typography>

                {/*     */}
                <Typography variant="h6" sx={{ textAlign: 'right', flexGrow: 1, bgcolor: 'black' }}>
                    {signinCheck ?
                        <a href='/' className='custom_a' onClick={handleLogout}>Logout</a>
                        :
                        <a href='/signin' className='custom_a'>Login</a>
                    }
                </Typography>
            </Toolbar>  

            {/* Drawer  */}
            <Drawer open={open} onClose={toggleDrawer} variant="persistent" >
                {DrawerList}
            </Drawer>
        </AppBar>
    );
}
