
import * as React from 'react';
import Box from '@mui/material/Box';
import Link from '@mui/material/Link';
import { Form, FormSpy } from 'react-final-form';
import Typography from './modules/components/Typography';
import AppFooter from './modules/views/AppFooter';
import AppForm from './modules/views/AppForm';
import { email, required } from './modules/form/validation';
import FormButton from './modules/form/FormButton';
import FormFeedback from './modules/form/FormFeedback';
import withRoot from './modules/withRoot';
import { useParams, useNavigate } from 'react-router-dom';
import { SERVER_URL } from '../Link';
import { Checkbox, Dialog, DialogActions, DialogContent, DialogTitle, FormControlLabel, TextField } from '@mui/material';

import AppBarCustom from './modules/components/AppBarCustom'; 

function SignUp() {
  const [sent, setSent] = React.useState(false);
  const [ user, setUser ] = React.useState({
    userId:"",
    passWord:"",
    passWordCheck:"",
    email:"",
    emailCheck:false,
    name:"",
    phone:"",
    address:""
  })

  const navigate = useNavigate();

  const [otp, setOTP] = React.useState();
  const [checkOTP, setCheckOTP] = React.useState();

  const [ openEmailAccess, setOpenEmaillAccess] = React.useState(false);
  
  const params = useParams();

  const validate = (values) => {
    const errors = required(['name','userId','passWord','passWordCheck','phone','address', 'email'], values);

    if (!errors.email) {
      const emailError = email(values.email);
      if (emailError) {
        errors.email = emailError;
      }
    }

    return errors;
  };

  
    React.useEffect(() => {
      if(params.hash !== undefined){ //    
        fetch(SERVER_URL + "user/checkemail?hash=" + params.hash,{
          method:"GET",
          credentials: "include",
        })
        .then(response => response.text())
        .then(data => {
            if(data === ""){
                //     ( ,   ) ->  
                navigate("/");
            }else if (data === ""){
                //    
            }else{
                setUser({
                    ...user,
                    email:data,
                    emailCheck:true,
                })
            }
        })
        .catch(error => console.log(error))
      }
    },[]);

  const handleSubmit = () => {
    setSent(true);
  };

  function handleOnChange(e) {
    setUser({
        ...user,
        [e.target.name] : e.target.value,
    })
  }

  function handleTrySignUP() {
    if(user.name === ""){
        alert("  ");
    }else if(user.userId === ""){
        alert("  ");
    }else if(user.passWord === ""){
        alert("  ");
    }else if(user.passWord !== user.passWordCheck){
        alert("   ");
    }else if(user.phone === ""){
      alert("  ");
    }else if(user.address === ""){
      alert("  ");
    }else if(user.emailCheck !== true){
        alert("   ");
    }else{
        //  
        fetch(SERVER_URL + "user/signup", {
            method:"POST",
            headers:{"Content-Type":"application/json"},
            body:JSON.stringify(user),
        })
        .then(response => response.text())
        .then(data => {
            alert(data);
            if(data === ""){
                navigate('/signin');
            }else if(data === ""){
              setUser({
                  userId:"",
                  passWord:"",
                  passWordCheck:"",
                  email:"",
                  emailCheck:false,
                  name:"",
                  phone:"",
                  address:""
              });
            }
        })
        .catch(error => console.log(error))
    }
  }

  function handleAccessEmail() {
    //  
    setUser({
        ...user,
        email:"",
    })
    setCheckOTP();
    setOpenEmaillAccess(true);
  }

  function handleOnChangeOTP (e) {
    setOTP(e.target.value);
  }

  function handleSendOTP () {
    if(user.email === ""){
        alert("  ")
    }else{
        fetch(SERVER_URL + "user/sendotp")
        .then(response => response.text())
        .then(data => {
            alert("OTP : " + data);
            console.log(data);
            setCheckOTP(data);

            //
            setOTP(data);
        })
        .catch(error => console.log(error))
    }
  }

  function handleCheckOTP () {
    if(checkOTP === otp && checkOTP !== undefined){
        alert(" ");
        setUser({
            ...user,
            emailCheck:true,
        })
        setOpenEmaillAccess(false);
    }else{
        alert("OTP  ");
        setOTP();
    }
  }

  return (
    <React.Fragment>

      <Dialog open={openEmailAccess}>
          <DialogTitle>
              <p>  </p>
          </DialogTitle>
          <DialogContent>
              <input placeholder=' ' type='text' name="email" value={user.email} onChange={handleOnChange}></input>
              <br></br>
              <p>OTP </p>
              <input placeholder='OTP ' type='text' value={otp} onChange={handleOnChangeOTP}></input>
          </DialogContent>
          <DialogActions>
              <button onClick={handleSendOTP}>OTP </button>
              <button onClick={handleCheckOTP}></button>
          </DialogActions>
      </Dialog>

      <AppBarCustom ></AppBarCustom>
      <AppForm>
        <React.Fragment>
          <Typography variant="h3" gutterBottom marked="center" align="center">
            
          </Typography>
          <Typography variant="body2" align="center">
            <Link href="/signin" underline="always">
               ?
            </Link>
          </Typography>
        </React.Fragment>
        <Form
          onSubmit={handleSubmit}
          subscription={{ submitting: true }}
          validate={validate}
        >
          {({ handleSubmit: handleSubmit2, submitting }) => (
            <Box component="form" onSubmit={handleSubmit2} noValidate sx={{ mt: 6 }}>
              <TextField
                label="name"
                fullWidth
                margin="normal"
                name='name'
                value={user.name}  // `user.name`  
                onChange={handleOnChange}  //    
                disabled={submitting || sent}
              />

              <TextField
                label="userId"
                fullWidth
                margin="normal"
                name='userId'
                value={user.userId}  // `user.name`  
                onChange={handleOnChange}  //    
                disabled={submitting || sent}
              />

              <TextField
                label="password"
                fullWidth
                margin="normal"
                name='passWord'
                type="password"
                value={user.passWord}  // `user.name`  
                onChange={handleOnChange}  //    
                disabled={submitting || sent}
              />

              <TextField
                label="password_check"
                fullWidth
                margin="normal"
                name='passWordCheck'
                type="password"
                value={user.passWordCheck}  // `user.name`  
                onChange={handleOnChange}  //    
                disabled={submitting || sent}
              />

              <TextField
                label="phone"
                fullWidth
                margin="normal"
                name='phone'
                type="text"
                value={user.phone}  // `user.name`  
                onChange={handleOnChange}  //    
                disabled={submitting || sent}
              />

              <TextField
                label="address"
                fullWidth
                margin="normal"
                name='address'
                type="text"
                value={user.address}  // `user.name`  
                onChange={handleOnChange}  //    
                disabled={submitting || sent}
              />
              
              <FormControlLabel
                disabled
                control={<Checkbox checked={user.emailCheck} />}
                label={user.emailCheck ? user.email : "   "}
              />
              
              <FormButton
                sx={{ mt: 3, mb: 2 }}
                disabled={submitting || sent}
                color="info"
                fullWidth
                onClick={handleAccessEmail}
              >
                {submitting || sent ? 'In progress' : ' '}
              </FormButton>

              <FormSpy subscription={{ submitError: true }}>
                {({ submitError }) =>
                  submitError ? (
                    <FormFeedback error sx={{ mt: 2 }}>
                      {submitError}
                    </FormFeedback>
                  ) : null
                }
              </FormSpy>
              <FormButton
                sx={{ mt: 3, mb: 2 }}
                disabled={submitting || sent}
                color="primary"
                fullWidth
                onClick={handleTrySignUP}
              >
                {submitting || sent ? 'In progress' : ''}
              </FormButton>
            </Box>
          )}
        </Form>
      </AppForm>
      <AppFooter />
    </React.Fragment>
  );
}

export default withRoot(SignUp);