import React, { useState, useEffect } from "react";

const EventPopup = ({ isOpen, onClose, event, setEvent, onChange }) => {

  function handleSave () {
    if (!event.name || !event.startTime || !event.endTime) {
      alert("  !");
      return;
    }

    if(event.id){
        //    
        onChange({
            ...event,
            doing:""
        });
    }else{
        //    
        onChange({
            ...event,
            doing:""
        });
    }

    onClose();
  };

  const handleDelete = () => {
    if (window.confirm(" ?")) {
        onChange({
            ...event,
            doing:""
        });
    }
  };

  if (!isOpen) return null;

  //  YYYY-MM-DD   
  function convertTimestampToDate(timestamp) {
    const date = new Date(timestamp);
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0'); //  0 
    const day = String(date.getDate()).padStart(2, '0');
    return `${year}-${month}-${day}`;
  }

  return (
    <div className="popup-overlay" onClick={onClose}>
      <div
        className="popup-content"
        onClick={(e) => e.stopPropagation()} //    
      >
        <h2>{event.id ? " " : "  "}</h2>
        <input
          type="text"
          placeholder=""
          value={event.name}
          onChange={(e) =>
            setEvent({ ...event, name: e.target.value })
          }
        />
        <input
          type="text"
          placeholder=""
          value={event.content}
          onChange={(e) =>
            setEvent({ ...event, content: e.target.value })
          }
        />
        <input
          type="date"
          value={convertTimestampToDate(event.startTime)}
          onChange={(e) =>
            setEvent({ ...event, startTime: e.target.value })
          }
        />
        <input
          type="date"
          value={convertTimestampToDate(event.endTime)}
          onChange={(e) =>
            setEvent({ ...event, endTime: e.target.value })
          }
        />
        <input
          type="color"
          value={event.color}
          onChange={(e) =>
            setEvent({ ...event, color: e.target.value })
          }
        />
        <div>
          <button onClick={handleSave}>
            {event.id ? "" : ""}
          </button>
            {event.id && (
                <button
                onClick={handleDelete}
                style={{ backgroundColor: "#dc3545", color: "white" }}
                >
                
                </button>
            )}
          <button onClick={onClose}></button>
        </div>
      </div>
    </div>
  );
};

export default EventPopup;
